<template>
    <v-app id="questionDetailPageApp">
        <div id="titleArea" class="backColorSetting">
            <v-layout>
                <v-flex sm3></v-flex>
                <v-flex sm7 class="verticalEnd toFlex" id="detailTitleArea">
                    <v-layout column>
                        <!-- Title -->
                        <v-layout id="subHeader">
                            <v-flex row>
                                <p id="keyword"><span>{{questionData.title}}{{questionData.title}}</span></p>
                            </v-flex>
                        </v-layout>
                        <!-- tag lists -->
                        <v-layout id="tagArea">
                            <v-flex>
                                <p class="tagListDetail"
                                    v-for="(tag, index) in this.questionData.tag"
                                    :key="index"
                                >
                                    {{tag}}
                                </p>
                            </v-flex>
                        </v-layout>
                    </v-layout>
                </v-flex>
            </v-layout>
        </div>
        <!--   -->
        <post :item="this.questionData"/>
        <div class="answerArea">{{questionData.answers.length}} </div>
        <!--   -->
        <div v-for="(item, index) in this.questionData.answers"
                  :key="index" >
            <answer :item="item"/>
            <div class="answerDivideLine"></div>
        </div>
    </v-app>
</template>

<script>
import questionService from '../services/question'
import post from '../components/post'
import answer from '../components/answer'

export default {
  name: 'questionDetailPage',
  components: {
    post,
    answer
  },
  props: {
    qid: { type: String }
  },
  data () {
    return {
      questionData: []
    }
  },
  created () {
    // this.getQuestionList()
    this.testQuestionList()
    this.questionData = this.questionData[0]
  },
  methods: {
    getQuestionList () {
      this.questionData = questionService.findQuestion(this.qid)
      this.questionData = questionService.convertToDate(this.questionData)
    },
    testQuestionList () {
      this.questionData = [
        {
          qid: '1',
          title: '    ',
          contents: 'Vue.js  .      ?',
          writerUid: '1',
          writerName: '',
          writerGrade: 7,
          tag: ['Vue.js', 'Javascript'],
          bClosed: false,
          timestamp: new Date().getTime(),
          score: Number(0),
          cntAnswer: 1,
          answers: [
            {
              aid: 'ans1',
              writerUid: 'user1',
              writerName: '',
              contents: ` Vue.js  RESTFUL APU .

   JSON  ,          .

   Vue.js  , Vue.js        .  Vuetify API     .           !

\`\`\`java
import java.io.*;

public class Main {
    public static void main(String[] args){
        System.out.println("Hello World");
    }
}
\`\`\`
`,
              timestamp: new Date().getTime(),
              score: Number(0),
              bSelection: false,
              comments: [],
              photoURL: '',
              writerGrade: 5,
            },
            {
              aid: 'ans1',
              writerUid: 'user1',
              writerName: '',
              contents: ` Vue.js  RESTFUL APU .

   JSON  ,          .

   Vue.js  , Vue.js        .  Vuetify API     .           !`,
              timestamp: new Date().getTime(),
              score: Number(0),
              bSelection: false,
              comments: [],
              photoURL: '',
              writerGrade: 3,
            }
          ],
          comments: [
            {
              cid: 'comm2',
              wrtierUid: 'user2',
              writerName: '',
              contents: '   ',
              timestamp: new Date().getTime()
            }],
          photoURL: 'https://cdn.vuetifyjs.com/images/john.jpg'
        }]

      this.questionData = questionService.convertToDate(this.questionData)
    }
  }
}
</script>

<style>
#questionDetailPageApp {
  background-color: white;
}

.v-application--wrap {
  min-height: 0vh;
}

#titleArea {
  min-height: calc(47vh - 64px);
  /* max-height: calc(47vh - 64px); */
  color: white;
}

#detailTitleArea {
  min-height: calc(47vh - 64px);
  margin: 0 0 2vh 4vw;
}

#subHeader {
  margin: 3vh 0px 0.5vh 0px;
}

#tagArea {
  margin: 10px 0 5px 0;
}

#keyword {
  font-size: 1.4em;
  margin: 0 0 0 10px;
}

#keyword span {
  font-size: calc(1.96vh + 1.88vw);
}

#questionMeta{
  color: grey;
  padding-bottom: 8px;
  margin-bottom: 16vh;
  border-bottom: 0.5px solid #e4e6e8;
}

.subData {
  margin-right: 13px;
  font-size: 0.87rem;
}

.timestamp {
  font-size: 0.92rem;
}

.answerArea {
  font-size: 1.32rem;
  width: 89vw;
  margin: auto;
  padding-top: 45px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.45);
}

.answerDivideLine {
  width: 86vw;
  margin: auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.24);
}
</style>



